<template>
	<div id="agent-finance-index">
		<public-header title="资产"></public-header>
		<div class="content">
			<div class="account bg-primary">
				<div class="font-14 margin-15-b">{{agent.title}}账户可用余额(元)</div>
				<div>¥{{agent.amount}}</div>
			</div>
			<van-cell-group class="margin-10-t">
				<van-cell title="提现" value="去提现" is-link class="border-0px" :to="util.getUrl({path: 'pages/plugin/agent/finance/getCash'})"></van-cell>
			</van-cell-group>
		</div>
		<iloading v-if="showPreLoading"></iloading>
	</div>
</template>

<script>
	import publicHeader from "@/components/header"
	import {getData} from "@/controller/funcCommon"
	export default {
		data() {
			return {
				agent: {
					title: '',
					amount: 0,
				},
				showPreLoading: true
			}
		},
		components: {
			publicHeader,
		},
		methods: {
			onLoad() {
				var that = this;
				getData({
					vue: that,
					url: 'plateform/agent/finance',
					autoAssign: true,
					variable: 'agent'
				})
			}
		},
		mounted() {
			this.onLoad();
		}
	}
</script>

<style>
	#agent-finance-index .account{
		text-align: center;
		font-size: 30px;
		padding: 40px 0;
	}
</style>


